<template>
  <div>
    <div class="room-selector text-center mt-2">
      <span class="h3">Room</span>
      <select
        class="form-control form-control-lg mt-2"
        @change="updateRoomIndex()"
        id="roomSelector"
      >
        <option v-for="room in eventData" :key="room.roomName">{{room.roomName}}</option>
      </select>
    </div>
    <div class="main-body-card mt-2 py-1 px-1 px-md-3 px-lg-5" v-if="eventData[selectionIndex]">
      <event-card
        v-for="event in eventData[selectionIndex].roomEvents"
        :key="event.id"
        :theEvent="event"
        :settings="settings"
      ></event-card>
    </div>
  </div>
</template>

<script>
import EventCard from "../components/roomView/EventCard";

export default {
  props: ["eventData", "settings"],
  components: {
    "event-card": EventCard
  },
  data() {
    return {
      selectionIndex: 0
    };
  },
  methods: {
    updateRoomIndex() {
      this.selectionIndex = this.$el.querySelector(
        "#roomSelector"
      ).selectedIndex;
    }
  }
};
</script>

