<template>
  <div class="event-card my-3 pt-2" :class="classes">
    <div class="row">
      <div class="col-5 col-sm-4 col-md-3 text-center">
        <div class="row h-100 text-right">
          <div class="col-sm-12 my-auto">
            <div>
              <p class="h3 mb-0">{{getLocalTime(theEvent.startTime)}}</p>
              <p class="muted mb-0">- {{getLocalTime(theEvent.endTime)}}</p>
              <p class="muted">{{getTimeTo()}}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="col-7 col-sm-8 col-md-9 col-xl-7 align-middle">
        <h5>{{theEvent.title}}</h5>
        <p>{{theEvent.description}}</p>
      </div>
    </div>
    <hr />
  </div>
</template>

<script>
import localTime from "../../functions/getLocalTime";
export default {
  props: ["theEvent", "settings"],
  data() {
    return {
      timeUntil: "",
      ended: false,
      classes: ""
    };
  },
  methods: {
    getLocalTime(timestamp) {
      return localTime.toLocaltime(timestamp, this.settings.timezone);
    },
    getTimeTo() {
      if (this.ended)
        return `Finished ${Math.floor(-this.timeUntil / 60)}H ${-this
          .timeUntil % 60}M Ago`;
      if (this.timeUntil > 0) {
        return `Event in ${Math.floor(this.timeUntil / 60)}H ${this.timeUntil %
          60}M`;
      } else {
        this.classes = "ongoing";
        return `Started ${Math.floor(-this.timeUntil / 60)}H ${-this.timeUntil %
          60}M Ago`;
      }
    }
  },
  created() {
    this.timeUntil = localTime.pastTime(this.theEvent.startTime);
    if (localTime.pastTime(this.theEvent.endTime) < 0) {
      this.ended = true;
      this.classes = "over";
    }
  }
};
</script>

<style scoped>
.event-card {
  border-radius: 5px;
}
.over {
  color: white;
  background-color: #ef4848;
}
.ongoing {
  color: white;
  background-color: #48c1ef;
}
</style>