<template>
  <div id="app">
    <app-navbar :navToggle="navToggle" @getEventData="getEventData()" @toggleNav="toggleNav()"></app-navbar>
    <app-navtoggle
      @toggleNav="toggleNav()"
      :class="{'btn-info': !navToggle, 'btn-secondary': navToggle}"
    ></app-navtoggle>
    <router-view
      class="app-body py-4 px-1 px-md-4"
      :class="{'navbar-active': !navToggle}"
      :eventData="eventData"
      :rawEventData="rawEventData"
      :settings="settings"
    ></router-view>
  </div>
</template>

<script>
import axios from "axios";
import localTime from "./functions/getLocalTime";

import Navbar from "./components/layout/Navbar";
import NavToggle from "./components/layout/NavToggle";

export default {
  name: "App",
  components: {
    "app-navbar": Navbar,
    "app-navtoggle": NavToggle
  },
  data() {
    return {
      navToggle: true,
      eventData: [],
      rawEventData: null,
      settings: {
        timezone: ""
      }
    };
  },
  methods: {
    toggleNav() {
      if (this.navToggle) return (this.navToggle = false);
      this.navToggle = true;
    },
    getEventData() {
      if (this.rawEventData === null) {
        return axios
          .get("https://schedule-api.ponyfest.horse/schedule/")
          .then(results => {
            this.rawEventData = results.data;
            console.log("got new data");

            console.log(results.data);
            this.formatEventData();
          })
          .catch(e => {
            console.log("Error: " + e);
          });
      }
      this.formatEventData();
    },
    formatEventData() {
      //Convert Room keys into array of rooms
      const roomArray = Object.keys(this.rawEventData.rooms);
      let cleanedData = [];

      roomArray.forEach(room => {
        cleanedData.push({
          roomName: room,
          roomEvents: this.rawEventData.rooms[room]
        });
      });

      this.eventData = cleanedData;

      console.log(this.eventData);
    }
  },
  created() {
    this.getEventData();
    this.settings.timezone = localTime.timezone();
  },
  watch: {
    $route() {
      this.navToggle = true;
    }
  }
};
</script>

<style>
@import "./assets/global.css";

.app-body {
  transition: all 0.4s;
}
@media screen and (min-width: 992px) {
  .app-body {
    margin-left: 20rem;
  }
}
</style>
