const moment = require('moment-timezone');
// const momenttz = require('moment-timezone');

module.exports = {
	timezone: function() {
		return moment.tz.guess();
	},
	toLocaltime: function(timestamp, timezone) {
		const input = moment.tz(timestamp, 'America/New_York');
		return input.tz(timezone).format('h:mm A');
	},
	pastTime: function(timestamp) {
		const input = moment.tz(timestamp, 'America/New_York');
		const currentTime = moment();
		return input.diff(currentTime, 'minutes');
	},
	diffTime: function(start, end) {
		return moment.tz(end, 'America/New_York').diff(moment.tz(start, 'America/New_York'), 'minutes');
	},
};
