<template>
  <button id="navToggle" class="btn btn-lg nav-toggle" @click="toggleNav()">Menu</button>
</template>

<script>
export default {
  methods: {
    toggleNav() {
      this.$emit("toggleNav");
    }
  }
};
</script>

<style scoped>
.nav-toggle {
  position: fixed;
  top: 1rem;
  left: 1rem;
  transition: all 0.4s;
  z-index: 2;
}

@media screen and (min-width: 992px) {
  .nav-toggle {
    display: none;
  }
}
</style>